.section-profiletwo {
  position: relative;
    height: 100vh;
    background-image: linear-gradient(rgba(0, 0, 0, 0.80), rgba(255, 255, 255, 0.80));
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    color: #fff;
    padding: 200px 20px 20px 20px;
    z-index: 1;
    display: flex;
    align-items: center;
    
  }

.container-enlaces{
  height: auto;
  margin: 0 200px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}  

.container-card1{
  background: rgba(0, 0, 0);
  padding: 40px;
  transition: transform .5s;
}

.container-card1:hover{
  transform: scale(1.2);
}

.container-card1 img{
  width: 100%;
  
}

