.section-fifth {
    position: relative;
    height: 100vh;
    background-image: linear-gradient(rgba(5, 10, 5, 0.80), rgba(0, 0, 0, 0.80));
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    color: #fff;
    padding: 80px 20px 20px 20px;
    z-index: 1;
    display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.section-fifth h2{
    color: rgba(255, 255, 255, 0.8);
    font-size: 40px;
    font-weight: 800;
    text-transform: uppercase;
    letter-spacing: 2px;
    line-height:  80p;
    text-shadow:  0 0 3px #fff, 0 0 3px #000;
    text-align: center;
}

.container-net{
    margin: 0 100px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr ;
}

.card-contac{
    position: relative;
    width: 300px;
    height: 400px;
    margin: 30px;
    box-shadow: 20px 20px 50px rgba(0, 0 ,0 ,0.5);
    border-radius: 15px;
    background: rgba(255, 255, 255, 0.1);
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-top: 1px solid rgba(255, 255, 255, 0.5);
    border-left: 1px solid rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(5px);
}

.card-img{
    width: 70%;
    height: 50%;
}

.card-img img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.card-info{
    margin: 20px;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.card-info p{
    font-size: 0.8em;
}

.card-info a{
    position: relative;
    display: inline-block;
    padding: 8px 20px;
    margin-top: 15px;
    background: #fff;
    color: #000;
    border-radius: 20px;
    text-decoration: none;
    font-weight: 500;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
}

.container-footer{
    height:100px;
    background-color: #000;
    margin: -20px;
    display: flex;
    justify-content: center;
    align-items: center;
}