.section-third{
    position: relative;
    height: 100vh;
    background-color: #fff;
    color: #000;
    z-index: 1;
    padding: 80px 20px 20px 20px;
    
  }

.section-third h2{
  color: rgba(0, 0, 0, 0.8);
  font-size: 40px;
  font-weight: 800;
  text-transform: uppercase;
  letter-spacing: 2px;
  line-height:  80p;
  text-shadow:  0 0 3px #fff, 0 0 3px #000;
  text-align: center;
}

.section-third h3{
  text-align: center;
  margin-top: 20px
}

hr{
    margin: 40px 300px;
}

.container-proyects{
  margin: 0 300px;
  height: calc(100% - 100px);
  display:flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.card-proyect1{
  max-width: 400px;
  height: 250px;
  background: #000;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0 3px 50px rgba(0,0,0,0.5);
  transition: 0.4s ease-in-out;
  
}

.card-proyect1:hover{
  height: 400px;

}

.cimg {
  position: relative;
  width: 250px;
  height: 300px;
  top: -20px;
  box-shadow: 0 3px 50px rgba(0,0,0,0.5);
}

.cimg img{
   max-width: 100%;
   border-radius: 6px;
   object-fit: cover;
   object-position: center center;
}

.cdetail{
  color: #fff;
  opacity: 1;
  transition: 0.4s ease-in-out;
  margin-top: -60px;
  padding: 0px 20px 10px 20px;
  opacity: 0;
}

.card-proyect1:hover .cdetail{
  opacity: 1;
}
