.section-skills{
    position: relative;
    height: calc(100vh - 50%);
    background-color: #fff;
    color: #000;
    z-index: 1;
    padding-bottom: 100px;
}

.section-skills h2{
    color: rgba(0, 0, 0, 0.8);
    font-size: 40px;
    font-weight: 800;
    text-transform: uppercase;
    letter-spacing: 2px;
    line-height:  80p;
    text-shadow:  0 0 3px #fff, 0 0 3px #000;
    text-align: center;
}

.section-skills h3{
    text-align: center;
    margin-top: 20px
  }

.container-skills{
    width: 100%;
    display:flex;
    justify-content: center;
    align-items: center;
}  

