.img_logo {
    display: flex;
  }
  
  .img_logo img {
    height: 40px;
    margin: 0 auto;
  }
  
  .img_logo b {
    color: white;
    font-family: "Segoe UI";
    margin-top: 10px;
    font-size: 1rem;
    margin-left: 20px;
  }
  
  header {
    z-index: 999;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 80px;
    transition: 0.6s;
  }
  
  header.down{
    background: rgba(0,0,0,.8);
    padding: 15px 70px;
  }
  
  /*header .img_logo {
    color: #fff;
    font-size:30px;
    font-weight: 700;
    text-transform: uppercase;
    text-decoration: none;
    letter-spacing: 2px;
  }*/
  
  header .menu{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  header .menu ul {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  header .menu ul li {
    list-style: none;
    display: inline-block;
    margin: 0 20px;
    position: relative;
  
  }
  
  header .menu ul li a {
    text-decoration: none;
    color: white;
    font-family: "Segoe UI";
    font-size: 16px/*1.2rem*/;
    text-transform: uppercase;
    font-weight: 500/*bold*/;
    /*margin: 0 30px; /*Si queda mal quitarlo pto*/
    /*padding: 7px 12px; /*Si queda mal quitarlo pto*/
    /*border-radius: 20px;
    transition: 3s;
    transition-property: color, background;*/
  }
  
  header ul li::before {
    content: "";
    height: 3px;
    width: 0%;
    background: yellowgreen;
    position: absolute;
    left: 0;
    bottom: -12px;
    transition: 0.4s ease-out;
  }
  
  header ul li:hover::before {
    width: 100%;
  }
  
  header .btn{
    color: #fff;
    font-size: 25px;
    cursor: pointer;
    display: none;
  }
  
  @media (max-width: 1280px) {
    header, header.down{
      padding: 20px 40px;
    }
    
    header .btn{
      display: block;
    }
  
    header .menu{
      position: fixed;
      background: rgba(0,0,0,.9);
      min-width: 33px;
      height: 100vh;
      top: 0;
      right: -100%;
      padding: 80px 50px;
      transition: 0.5s;
      transition-property: right;
    }
  
    header .menu ul{
      flex-direction: column;
    }
  
    header .menu.active{
      right:0;
    }
  
    header .menu .close-btn{
      position: absolute;
      top: 0;
      left: 0;
      margin: 25px;
    }
  
    header .menu a{
      display: block;
      
    }
  
    header .menu ul li{
      margin: 0 0 20px 0;
    }
  }
  
  @media (max-width:600px){
    .section-main h1{
      font-size: 40px;
      line-height: 60px;
    }
  }