.section-two {
  position: relative;
    height: 100vh;
    /*background-image: linear-gradient(rgba(0, 0, 0, 0.80), rgba(255, 255, 255, 0.80));*/
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    padding-top: 80px;
    color: #fff;
    z-index: 2;
  }

.section-two h2{
  color: rgba(255, 255, 255, 0.8);
  font-size: 40px;
  font-weight: 800;
  text-transform: uppercase;
  letter-spacing: 2px;
  line-height:  80p;
  text-shadow:  0 0 3px #fff, 0 0 3px #000;
  text-align: center;
}

hr{
    margin: 40px 300px;
}

.container-profile{
  height: calc(100% - 80px);
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  margin: 0px 300px;
}

.card-pitch{
  position: relative;
    width: 650px;
    height: auto;
    padding: 30px;
    margin: 0 auto;
    box-shadow: 20px 20px 50px rgba(0, 0 ,0 ,0.5);
    border-radius: 15px;
    background: rgba(255, 255, 255, 0.1);
    border-top: 1px solid rgba(255, 255, 255, 0.5);
    border-left: 1px solid rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(5px);

    display: grid;
}

.card-pitch img{
  margin: 0px auto;
}

.card-pitch p{
  font-size: 1em;
  color: #fff;
  font-weight: 300;
  margin-top: 0.5em;
  text-align:justify;
  letter-spacing: 1px;
  line-height:  20px;

}

.cards-profile{
  display: flex;
  padding: 40px;
}

.card-competitions{
  position: relative;
    width: 650px;
    height: auto;
    padding: 20px;
    margin: 0 auto;
    box-shadow: 20px 20px 50px rgba(0, 0 ,0 ,0.5);
    border-radius: 15px;
    background: rgba(255, 255, 255, 0.1);
    border-top: 1px solid rgba(255, 255, 255, 0.5);
    border-left: 1px solid rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(5px);

    display: grid;
}

.card-competitions img, h3{
  margin: 0px auto;
}

