.card-content{
  margin: 0 auto;
}

.card-content li{
  font-size: 1em;
  color: #fff;
  font-weight: 300;
  margin-top: 0.5em;
  text-align:justify;
  letter-spacing: 1px;
  line-height: 1.7em;
}

.card-content p {
  font-size: 1em;
  color: #fff;
  font-weight: 300;
  margin-top: 0.5em;
  text-align:justify;
  letter-spacing: 1px;
