.section-one {
    position: relative;
    height: 100vh;
    /*background-image: linear-gradient(rgba(5, 10, 5, 0.80), rgba(90, 40, 90, 0.80));*/
    color: #fff;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    padding: 80px 20px 20px 20px;
    z-index: 1;
}

.section-one h2{
    color: rgba(255, 255, 255, 0.8);
    font-size: 40px;
    font-weight: 800;
    text-transform: uppercase;
    letter-spacing: 2px;
    line-height:  80p;
    text-shadow:  0 0 3px #fff, 0 0 3px #000;
    text-align: center;
}

hr{
    margin: 40px 300px;
}

.container-inicio{
    margin: 0px 300px;
    height: 80%;
    display: grid;
    grid-template-columns: 1fr 3fr;
    gap: 30px;
}

.container-picture{
    display: flex;
    align-items: center;
    justify-content: center;
}

.profile-picture{
    width: 400px; /* you can use % */
    height: 400px;
    margin: 0px 40px;
    transition: transform .5s;
    border-radius: 10%;
}

.profile-picture:hover{
    transform: scale(1.1);
}

.container-info{
    height: 400px;
}

.container-info h3{
    color: #fff;
    font-size: 25px;
    font-weight: 800;
    text-transform: uppercase;
    letter-spacing: 2px;
    line-height:  50px;
    text-shadow:  0 0 3px #fff, 0 0 3px #000;
    text-align: center;
}

.container-details{
    margin-top: 40px;
    
}

.container-details h4{
    font-size: 20px;
    text-transform: uppercase;
    text-align: center;
}

.card-details{
    position: relative;
    width: 650px;
    height: auto;
    padding: 30px;
    margin: 0 auto;
    box-shadow: 20px 20px 50px rgba(0, 0 ,0 ,0.5);
    border-radius: 15px;
    background: rgba(255, 255, 255, 0.1);
    border-top: 1px solid rgba(255, 255, 255, 0.5);
    border-left: 1px solid rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(5px);
}

.card-details p{
    font-size: 1em;
    color: #fff;
    font-weight: 300;
    margin-top: 0.5em;
    text-align:justify;
    letter-spacing: 1px;
    line-height:  20px;
  }


.container-contact{
    display:flex;
    justify-content: center;
    align-items: center;
    height: 70%;
}

.container-contact ul{
    position: relative;
    display: flex;
    transform-style: preserve-3d;
    transform: rotate(-25deg) skew(25deg);
  }
  
.container-contact ul li{
    position: relative;
    list-style: none;
    width: 60px;
    height: 60px;
    margin:  0 10px;
}

.container-contact ul li:before{
    position: absolute;
    content: '';
    bottom: -10px;
    left: 0;
    width: 100%;
    height: 10px;
    background: #2a2a2a;
    transform-origin: top;
    transform: skew(-41deg);
}

.container-contact ul li::after{
    position: absolute;
    content: '';
    top: 0px;
    left: -9px;
    width: 9px;
    height: 100%;
    background: #2a2a2a;
    transform-origin: right;
    transform: skewY( -49deg);
}

.container-contact ul li span {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex !important;
    justify-content:center;
    align-items: center;
    background: #333;
    color: rgba(255,255,255,0.2);
    font-size: 30px !important;
    transition: 0.2s;
}

.container-contact ul li:hover span{
    z-index: 1000;
    transition: 0.5s;
    color: #fff;
    box-shadow: -1px 1px 1px rgb(0, 0, 0, 0.05);
}

.container-contact ul li:hover span:nth-child(5){
    transform: translate(40px, -40px);
    opacity: 1;
}

.container-contact ul li:hover span:nth-child(4){
    transform: translate(30px, -30px);
    opacity: 0.8;
}

.container-contact ul li:hover span:nth-child(3){
    transform: translate(20px, -20px);
    opacity: 0.6;
}

.container-contact ul li:hover span:nth-child(2){
    transform: translate(10px, -10px);
    opacity: 0.4;
}

.container-contact ul li:hover span:nth-child(1){
    transform: translate(0px, 0px);
    opacity: 0.2;
}

.container-contact ul li:hover span:nth-child(1){
    transform: translate(0px, 0px);
    opacity: 0.2;
}

.container-contact ul li:nth-child(1):hover span{
    background: #eb0a0a;
}

.container-contact ul li:nth-child(2):hover span{
    background: #25d366;
}

.container-contact ul li:nth-child(3):hover span{
    background: #0077b5;
}


/*Ventana minimizada*/
@media (max-width: 1280px) {
    hr{
        margin: 40px 80px;
    }

    .container-inicio{
        margin: 0px 80px;
        /*flex-wrap: wrap;*/
    } 

    .profile-picture{
        height: 200px;
    }
}

/*Vista de cecular*/
@media (max-width:600px){

    .container-inicio{
        margin: 0px 30px;
    
    }

    .profile-picture{
        height: 200px;
    }
}