/*Seccion Main*/

.section-main{
    position: relative;
    width: 100%;
    min-height: 100vh;
    /*background: linear-gradient(to left, rgba(0,0,0,.8), rgba(0,0,0,.1)),url(https://wallpaperbat.com/img/55242-anime-boy-wallpaper-top-free-anime-boy-background.png) no-repeat;*/ 
    /*background: linear-gradient(to left, rgba(0,0,0,.8), rgba(0,0,0,.1)),url(http://www.solofondos.com/wp-content/uploads/2016/10/cubo-de-rubik-artistica-papel-pintado-2560x960-39821_87.jpg) no-repeat*/;
    background: linear-gradient(to left, rgba(0,0,0,.8), rgba(0,0,0,.1)),url(https://midu.dev/images/wallpapers/una-taza-de-javascript.png) no-repeat;
    background-size: cover;
    background-position: center;
    display: grid;
    justify-content: center;
    align-items: center;
    padding: 20px 80px;
    z-index: 1;
  }
  
  .section-main h1{
    color: rgba(255, 255, 255, 0.8);
    font-size: 60px;
    font-weight: 800;
    text-transform: uppercase;
    letter-spacing: 2px;
    line-height:  80p;
    text-shadow:  0 0 3px #fff, 0 0 3px #000;
    margin-top: 200px;
  }
  
  .section-main .wrap{
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .section-main b{
    text-align: center;
    color: rgba(255, 255, 255, 0.8);
    font-size: 30px;
    font-weight: 800;
    text-transform: uppercase;
    letter-spacing: 2px;
    line-height:  80p;
    text-shadow:  0 0 3px #fff, 0 0 3px #000;
    margin-top: -200px;
  }
  
  .btn-empezar{
    min-width: 300px;
    min-height: 60px;
    font-family: 'Nunito', sans-serif;
    font-size: 22px;
    text-transform: uppercase;
    letter-spacing: 1.3px;
    font-weight: 700;
    color: #C4C4C4  ;
    background: #FFFFFF;
    /*background: linear-gradient(90deg, rgba(129,230,217,1) 0%, rgba(79,209,197,1) 100%);*/
    background: linear-gradient(90deg, rgba(57, 57, 57  ,1) 0%, rgba(0, 0, 0 ,1) 100%);
    border: none;
    border-radius: 1000px;
    /*box-shadow: 12px 12px 24px rgba(79,209,197,.64);*/
    box-shadow: 12px 12px 24px rgba(0, 0, 0,.64);
    transition: all 0.3s ease-in-out 0s;
    cursor: pointer;
    outline: none;
    position: relative;
    padding: 10px;
  }
  
  .btn-empezar::before{
    content: '';
    border-radius: 1000px;
    min-width: calc(300px + 12px);
    min-height: calc(60px + 12px);
    /*border: 6px solid #00FFCB;
    box-shadow: 0 0 60px rgba(0,255,203,.64);*/
    border: 4px solid #FFFFFF ;
    box-shadow: 0 0 60px rgba(255, 255, 255,.64);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0;
    transition: all .3s ease-in-out 0s;
  }
  
  .btn-empezar:hover, .btn-empezar:focus{
    color: #FFFFFF ;
    transform: translateY(-6px);
  }
  
  .btn-empezar:hover::before, .btn-empezar:focus::before{
    opacity: 1;
  }
  
  .btn-empezar::after{
    content: '';
    width: 30px; height: 30px;
    border-radius: 100%;
    border: 6px solid #FFFFFF;
    position: absolute;
    
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    animation: ring 1.5s infinite;
  }
  
  .btn-empezar:hover::after, .btn-empezar:focus::after{
    animation: none;
    display: none;
  }
  
  @keyframes ring {
    0% {
      width: 20px;
      height: 20px;
      opacity: 1;
    }
    100% {
      width: 200px;
      height: 200px;
      opacity: 0;
    }
  }