* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Verdana, Geneva, Tahoma, sans-serif; 
}

::-webkit-scrollbar{
  width: 10px;
  background-color: #fff;
}
::-webkit-scrollbar-track{
  background-color: #000;
}
::-webkit-scrollbar-thumb{
  border-radius: 5px;
  background-color: #fff;
}

