    position: relative;
    
    max-height: auto !important;
    background: #000;
    z-index: 1;
    padding: 80px 20px 20px 20px;
    color: #fff;
  }

.section-fourth h2{
  color: rgba(255, 255, 255, 0.8);
  font-size: 40px;
  font-weight: 800;
  text-transform: uppercase;
  letter-spacing: 2px;
  line-height:  80p;
  text-shadow:  0 0 3px #fff, 0 0 3px #000;
  text-align: center;
}

