  .container-cards{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
    height: auto;
    z-index: 1;
  }
  
.card{
    position: relative;
    width: 300px;
    height: 500px;
    margin: 30px;
    box-shadow: 20px 20px 50px rgba(0, 0 ,0 ,0.5);
    border-radius: 15px;
    background: rgba(255, 255, 255, 0.1);
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    border-top: 1px solid rgba(255, 255, 255, 0.5);
    border-left: 1px solid rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(5px);
  }
  
.card .content{
    padding: 20px;
    text-align: center;
  }
  
.card img{
  width: 300px;
  margin-bottom: 15px;
}
  
.card h2{
    position: absolute;
    top: -30px;
    right: 30px;
    font-size: 8em;
    color: rgba(255, 255, 255, 0.05);
    pointer-events: none;
  }
  
.card h3{
    font-size: 1em;
    color: #fff;
    z-index: 1;
    padding: 0 8px;
  }
  
.card p{
    font-size: 0.8em;
    color: #fff;
    font-weight: 300;
    text-align: justify;
    padding: 0px 15px;
  }
  
.card a{
    position: relative;
    display: inline-block;
    padding: 8px 20px;
    margin-top: 15px;
    background: #fff;
    color: #000;
    border-radius: 20px;
    text-decoration: none;
    font-weight: 500;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
    transition: 0.5s;
  
    transform: translateY(100px);
    opacity: 0;
  }

  .card:hover a{
    transform: translateY(0px) !important;
    opacity: 1;
}
  
  